'use client';
import React, {useEffect, useRef} from 'react';
import {motion, AnimatePresence} from 'framer-motion';

interface RainDropProps {
    delay: number;
    left: string;
    size: number;
    speed: number;
    angle: number;
    startY: number;
}

const Cloud: React.FC = () => (
    <motion.div
        initial={{opacity: 0, scale: 0.8, y: -10}}
        animate={{opacity: 1, scale: 1, y: 0}}
        exit={{opacity: 0, scale: 0.8, y: -10}}
        transition={{duration: 0.4}}
        className="absolute -top-20 left-1/2 -translate-x-1/2 z-10"
    >
        <div className="relative">
            {/* Ana bulut gövdesi */}
            <div className="w-32 h-10 bg-gradient-to-br from-white to-gray-200 rounded-[2rem] shadow-lg" />
            {/* Üst bulut parçaları */}
            <div className="absolute -top-3 left-6 w-16 h-10 bg-gradient-to-br from-white to-gray-200 rounded-[2rem]" />
            <div className="absolute -top-5 left-12 w-14 h-10 bg-gradient-to-br from-white to-gray-200 rounded-[2rem]" />
            <div className="absolute -top-2 right-6 w-12 h-8 bg-gradient-to-br from-white to-gray-200 rounded-[2rem]" />
            {/* Alt bulut parçaları */}
            <div className="absolute -bottom-2 left-4 w-14 h-8 bg-gradient-to-br from-white to-gray-200 rounded-[2rem]" />
            <div className="absolute -bottom-1 right-5 w-12 h-7 bg-gradient-to-br from-white to-gray-200 rounded-[2rem]" />
        </div>
    </motion.div>
);

const RainDrop: React.FC<RainDropProps> = ({delay, left, size, speed, angle, startY}) => (
    <motion.div
        initial={{y: startY, x: 0, opacity: 0, scale: 1}}
        animate={{
            y: startY + 180,
            x: 80,
            opacity: [0, 0.85, 0.85, 0],
            scale: [1, 1.05, 1, 0.9]
        }}
        transition={{
            duration: speed,
            delay,
            repeat: Infinity,
            ease: "linear"
        }}
        className="absolute"
        style={{
            left,
            width: size,
            height: size * 2,
            transform: `rotate(${angle}deg)`,
            background: 'linear-gradient(180deg, rgba(147,197,253,0.95) 0%, rgba(59,130,246,0.85) 100%)',
            borderRadius: '30% 30% 45% 45%',
            boxShadow: `
                0 0 3px rgba(147,197,253,0.9),
                0 0 5px rgba(59,130,246,0.7)
            `
        }}
    />
);

interface RainEffectProps {
    isActive: boolean;
}

export const RainEffect: React.FC<RainEffectProps> = ({isActive}) => {
    const containerRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (!isActive || !containerRef.current) return;
        // Component mount edildiğinde yapılacak işlemler
        return () => {
            // Component unmount edildiğinde yapılacak temizlik işlemleri
        };
    }, [isActive]);

    if (!isActive) return null;

    const RAIN_ANGLE = -35;
    const CLOUD_HEIGHT = 80;

    return (
        <div ref={containerRef} className="absolute inset-0 overflow-hidden pointer-events-none">
            <AnimatePresence>
                <Cloud />
                {Array.from({length: 45}).map((_, i) => {
                    const size = Math.random() * 1.5 + 2;
                    const speed = Math.random() * 0.15 + 0.35;
                    const angleVariation = Math.random() * 6 - 3;
                    const leftPosition = Math.random() * 40 + 30;
                    const startY = CLOUD_HEIGHT - 10 + (Math.random() * 10);
                    
                    return (
                        <RainDrop
                            key={i}
                            delay={Math.random() * 0.8}
                            left={`${leftPosition}%`}
                            size={size}
                            speed={speed}
                            angle={RAIN_ANGLE + angleVariation}
                            startY={startY}
                        />
                    );
                })}
            </AnimatePresence>
        </div>
    );
}; 